.questions-container {
  background-color: #fff;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
.qusetions {
  position: relative;
  width: 600px;
  background: #fff;
  padding: 30px 25px;

  overflow: hidden;
  /* box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23); */
}
.question-title {
  text-align: center;
  margin-top: 30px !important;
}
.question-text {
  font-weight: bold;
  margin: 30px 0;
  font-size: 16px;
}
.checkbox-wrapper {
  display: flex;
  margin-bottom: 20px;
}
.checkmark {
  display: block;
  width: 25px;
  height: 25px;
  background-color: #ddd;
  border-radius: 5px;
  position: relative;
  transition: background-color 0.4s ease;
}
.checkmark::after {
  content: "";
  position: absolute;
  width: 7px;
  height: 15px;
  border-right: 3px solid #fff;
  border-bottom: 3px solid #fff;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%) rotateZ(40deg) scale(0.1);
  opacity: 0;
  transition: all 0.4s;
}
.option-text {
  line-height: 25px;
  padding-left: 20px;
  max-width: 90%;
}
#whatsapp:checked ~ .checkmark {
  background-color: #08bb68;
}
#whatsapp:checked ~ .checkmark::after {
  opacity: 1;
  transform: translate(-50%, -50%) rotateZ(40deg) scale(1);
}
