.custom-textarea {
  border: 1px solid rgba(0, 0, 0, 0.3);
  resize: none;
  height: 70px;
  width: 100%;
  border-radius: 10px;
  margin-bottom: 20px;
  padding: 10px;
}
.custom-textarea:focus {
  outline: none;
  border: 2px solid rgba(0, 0, 0, 0.6);
}
.thumb-container {
  display: flex;
  width: 100%;
  justify-content: center;
  margin: 20px 0;
  gap: 10px;
}
.thumbnail {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #ddd;
}
