.App {
  font-family: "Lato", sans-serif;
}
.navbar {
  height: 60px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 30px;
  background-color: #fff;
}
.logo {
  width: 150px;
}
.home-header {
  font-size: 24px;
  color: #1b1c1d;
  font-weight: 600;
  margin-top: 30px !important;
  margin-bottom: 0;
}
.para-text {
  font-size: 13px;
  font-weight: 500;
  margin: 10px 0 40px 0;
  color: #1b1c1d;
  padding: 0 15px;
}
.home-button {
  text-decoration: none;
  color: #1b1c1d;
  font-size: 16px;
  padding: 15px;
  border: 2px solid rgba(0, 0, 0, 0.6);
  margin-top: 15px;
}
.home-button:hover {
  color: #fff;
}
.main-container {
  width: 100%;
  margin: auto;
}
.video {
  width: 100%;

  min-height: 250px;
  background: #f9f9f9;
}

.home-container {
  text-align: center;
  background-color: #fff;
}
.video-container {
  padding-bottom: 30px;
  padding-top: 20px;
  position: relative;
}
.video-play-button {
  position: absolute;
  z-index: 10;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  box-sizing: content-box;
  border-radius: 10px;
  padding: 18px 20px 18px 28px;
  border: none;
  color: #fff;
  cursor: pointer;
  background: transparent;
  width: 0;
}
.video-play-button:focus {
  outline: none;
}
.video-play-button:before {
  content: "";
  position: absolute;
  z-index: 0;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  display: block;
  width: 80px;
  height: 80px;
  background: #ba1f24;
  border-radius: 50%;
  -webkit-animation: pulse-border 1500ms ease-out infinite;
  animation: pulse-border 1500ms ease-out infinite;
}

.video-play-button:after {
  content: "";
  position: absolute;
  z-index: 1;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  display: block;
  width: 80px;
  height: 80px;
  background: #fa183d;
  border-radius: 50%;
  transition: all 200ms;
}

.video-play-button:hover:after {
  background-color: #da0528;
}

.video-play-button img {
  position: relative;
  z-index: 3;
  max-width: 100%;
  width: auto;
  height: auto;
}

.video-play-button span {
  display: block;
  position: relative;
  z-index: 3;
  width: 0;
  height: 0;
  border-left: 32px solid #fff;
  border-top: 22px solid transparent;
  border-bottom: 22px solid transparent;
}

.video-play-button > .button-text {
  z-index: 3;
  display: none;
}

@-webkit-keyframes pulse-border {
  0% {
    transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1);
    opacity: 1;
  }
  100% {
    transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1.5);
    opacity: 0;
  }
}

@keyframes pulse-border {
  0% {
    transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1);
    opacity: 1;
  }
  100% {
    transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1.5);
    opacity: 0;
  }
}
.form-side-text-container {
  height: 100%;
  width: 100%;
  background: #1976d2;

  padding: 30px 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.form-side-text {
  font-size: 16px;
  line-height: 2rem;
  color: rgba(255, 255, 255, 0.8);
  text-align: justify;
  padding-top: 25px;
}
.form-side-image {
  margin: auto;
}
.form-side-container {
  text-align: center;
}
.user-container {
  background-color: #fff;
  width: 100%;
}
.form-container {
  background: #fff;
  padding: 30px;
}
.form-header {
  text-align: center;
  margin-top: 20px !important;
  margin-bottom: 40px !important;
  color: #202124;
  font-size: 24px;
  font-weight: 400;
  line-height: 1.3333;
}
.form-input {
  position: relative;
  height: 50px;
  overflow: hidden;
  margin-top: 30px;
}
.validation-text {
  margin-bottom: 40px;
  color: #c62828;
}
.form-input input {
  width: 100%;
  height: 100%;
  padding-top: 15px;
  border: none;
  outline: none;
}
.form-input label {
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  pointer-events: none;
  border-bottom: 1px solid rgb(212, 212, 212);
  font-weight: 500;
}
.form-input label::after {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  border-bottom: 2px solid #3482ea;
  left: 0px;
  bottom: -1px;
  transform: translateX(-100%);
  transition: transform 0.3s ease;
}
.content-name {
  position: absolute;
  bottom: 5px;
  left: 0px;
  transition: all 0.3s ease;
}
.form-input input:focus + .label-name .content-name,
.form-input input:valid + .label-name .content-name {
  transform: translateY(-150%);
  color: #3482ea;
  font-weight: 500;
}

.form-input input:focus + .label-name::after,
.form-input input:valid + .label-name::after {
  transform: translate(0%);
}

.small-text {
  text-align: center;
  margin-bottom: 20px;
  font-size: 14px;
  color: #5c5c5c;
  padding-bottom: 30px;
  padding-top: 30px;
}

.problem-container {
  width: 100%;
  background: #eaebec;
  margin-top: 10px;
  padding: 40px 20px;
}
.grid-container {
  width: 90%;
  margin: auto;
  margin-top: 40px;
}
.home-image {
  width: 100px;
}
.image-div {
  text-align: center;
  padding: 30px 0 0 0;
}
.image-title {
  font-size: 16px;
  font-weight: 600;
  margin-top: 15px;
  color: #1b1c1d;
}
.help-us-btn-container {
  margin-top: 100px;
}
.home-header-2 {
  font-size: 24px;
  color: #1b1c1d;
  font-weight: 600;
  margin-bottom: 0;
}
.btn {
  position: relative;
  display: block;
  margin: 60px auto;
  padding: 0;
  overflow: hidden;
  border-width: 0;
  outline: none;
  border-radius: 20px;
  font-size: 14px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.6);
  color: #ecf0f1;
  transition: background-color 0.3s;
}

.btn:hover,
.btn:focus {
  background-color: #27ae60;
}

.btn > * {
  position: relative;
}

.btn span {
  display: block;
  padding: 12px 24px;
}

.btn:before {
  content: "";

  position: absolute;
  top: 50%;
  left: 50%;

  display: block;
  width: 0;
  padding-top: 0;

  border-radius: 100%;

  background-color: rgba(236, 240, 241, 0.3);

  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.btn:active:before {
  width: 120%;
  padding-top: 120%;

  transition: width 0.2s ease-out, padding-top 0.2s ease-out;
}
.btn.red {
  background-color: #e74c3c;
}

.btn.red:hover,
.btn.red:focus {
  background-color: #c0392b;
  cursor: pointer;
}
.middle-text-container {
  padding: 0 10px;
}
.middle-text {
  font-size: 16px;
  line-height: 2.5rem;
  text-align: justify;
}
.presentation-image {
  width: 180px;
}
@media only screen and (min-width: 700px) {
  .main-container {
    width: 80%;
  }
  button.learn-more {
    width: 32rem;
  }
  .button-text {
    font-size: 14px;
  }
  .home-header {
    font-size: 30px;
    margin-top: 40px !important;
  }
  .home-header-2 {
    font-size: 24px;
  }
  .video {
    border-radius: 10px;
  }
  .main-container {
    padding-top: 30px;
  }
  .form-side-text-container {
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    padding: 30px 20px;
  }
  .form-side-text {
    font-size: 16px;
    padding-top: 25px;
  }
  .user-container {
    border-radius: 10px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    width: 95%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
  }
  .form-container {
    background: #fff;
    padding: 30px 40px 30px 10px;
    border-radius: 10px;
  }
}
@media only screen and (min-width: 1200px) {
  .main-container {
    width: 80%;
  }
  .video {
    border-radius: 10px;
    min-height: 50vh;
  }
  .home-header {
    font-size: 40px;
  }
  .grid-container {
    width: 90%;
  }
  .image-div {
    padding: 20px 30px 0 30px;
  }
  .home-header-2 {
    font-size: 30px;
  }
  .btn {
    font-size: 18px;
  }
  .problem-container {
    padding: 70px 20px;
  }
  .middle-text-container {
    padding: 40px 30px;
  }
  .middle-text {
    font-size: 18px;
    line-height: 2.6rem;
  }
  .grid-container {
    margin-top: 60px;
  }
  .form-side-text-container {
    padding: 30px 50px;
  }
  .form-side-text {
    font-size: 17px;
    padding-top: 25px;
  }
  .user-container {
    width: 80%;
  }
}
@media only screen and (min-width: 1600px) {
  .main-container {
    width: 70%;
  }
  .para-text {
    font-size: 16px;
  }
  .home-header {
    font-size: 54px;
  }
  .btn {
    font-size: 20px;
  }
  .middle-text-container {
    padding: 50px 40px;
  }
  .middle-text {
    font-size: 20px;
    line-height: 2.8rem;
  }
  .grid-container {
    margin-top: 75px;
  }
  .form-side-text-container {
    padding: 30px 60px;
  }
  .form-side-text {
    font-size: 19px;
    padding-top: 25px;
    line-height: 2.7rem;
  }
  .user-container {
    width: 70%;
  }
  .problem-container {
    padding: 100px 20px;
  }
}

button {
  position: relative;
  display: inline-block;
  cursor: pointer;
  outline: none;
  border: 0;
  vertical-align: middle;
  text-decoration: none;
  background: transparent;
  padding: 0;
  font-size: inherit;
  font-family: inherit;
}
button.learn-more {
  width: 3rem;
  height: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}
button.learn-more .circle {
  transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
  position: relative;
  display: block;
  margin: 0;
  width: 3rem;
  height: 3rem;
  background: #282936;
  border-radius: 1.625rem;
}

button.learn-more .circle i {
  position: absolute;
  top: 25%;
  left: 15px;
  color: #fff;
  font-size: 20px;
}

button.learn-more .circle .icon {
  transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  background: #fff;
}
button.learn-more .circle .icon.arrow {
  transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
  left: 0.625rem;
  width: 1.125rem;
  height: 0.125rem;
  background: none;
}
button.learn-more .circle .icon.arrow::before {
  position: absolute;
  content: "";
  top: -0.25rem;
  right: 0.0625rem;
  width: 0.625rem;
  height: 0.625rem;
  border-top: 0.125rem solid #fff;
  border-right: 0.125rem solid #fff;
  transform: rotate(45deg);
}
button.learn-more .button-text {
  transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 0.75rem 0;
  margin: 0 0 0 1rem;
  color: #282936;
  font-weight: 700;
  line-height: 1.6;
  text-align: center;
  text-transform: uppercase;
  display: none;
}

button.learn-more:hover {
  width: 20rem;
}

button:hover .circle,
button.learn-more:hover .button-text {
  width: 100%;
  display: inline-block;
}

button:hover .circle .icon.arrow {
  background: #fff;
  transform: translate(1rem, 0);
}
button:hover .button-text {
  color: #fff;
}
